<template>
   <div>
     <p class="page-title">当前位置:销售商管理-销售商详情</p>
        <div class="info">
                <div>
                   <div class="list"><span>真实姓名 ：</span>{{project.userName}}</div>
                      <div class="list"><span>手机号：</span> {{project.phoneNumber}}</div>
                       <div class="list"><span>身份证号：</span> {{project.identity}}</div>
                      <div class="list"><span>联系地址：</span> {{project.address}}</div>
                </div>
                <div class="tupian">
                    <div><img @click="showimgfunc(project.pic1)" :src="project.pic1" alt="">
                    </div>
                    <div><img @click="showimgfunc(project.pic2)" :src="project.pic2" alt="">
                    </div>
                     <div><img @click="showimgfunc(project.pic3)" :src="project.pic3" alt="">
                    </div>
                </div>
        </div>
    <div class="nav-tab">
          <span :class="{on:on==1}"@click="tabclick(1)">正在拼单</span>
          <span :class="{on:on==2}"@click="tabclick(2)">已完成拼单</span>
          <span :class="{on:on==3}"@click="errors()">待结算</span>
          <span :class="{on:on==3}"@click="errors()">已结算</span>
          <span :class="{on:on==3}"@click="errors()">提现</span>
          <span :class="{on:on==3}"@click="errors()">退款</span>
      </div>
        <Table border :columns="columns1" :data="data1"></Table>




        <Modal title="View Image" v-model="visible">
        <img :src="showimg" v-if="showimg"  style="width: 100%">
    </Modal>

   </div>
</template>
<script>
let        self = null;
 
export default {
    data(){
        return{
              columns1: [
                    {
                        title: '商品名称',
                        key: 'name'
                    },
                    {
                        title: '商品单价',
                        key: 'age'
                    },
                    {
                        title: '拼单人数',
                        key: 'address'
                    },
                    {
                        title: '拼单总金额',
                        key: 'address'
                    }
                ],
                data1: [],
           
            on:'1',
            showimg:"",
        visible:false,
        animal:'1',
        project:{},
      msg: 'Welcome to Your Vue.js App'
        }
    },
    methods:{
        getPageinit(){
                var openid = this.project.openid
                this.$api.wxgetActive({
                    data:{
                        current_page:this.page,
                        type:this.on,
                        openid:openid},
                    success:res=>{
                        self.data1 = res.data.orderlist
                        console.log(res)
                    }
                })


        },
        tabclick(e){
            this.on=e
        },
        errors(){
            this.$Message.error('该功能研发中');
        },
         showimgfunc(d){
          this.showimg = d;
            this.visible = true
      },
    },
    mounted(){
        self = this;
       var query = this.$route.query.data;
        this.project = JSON.parse(query)
        console.log(query)
        this.getPageinit()
    }
}
</script>
<style scoped>
.info{
    border: 1px solid #ddd;
    margin: 20px auto;
    width: 950px;
    min-height:  200px;
    border-radius: 10px;
}
.info>div{
    display: flex;

}
.info>div>div{
    width: 250px;
    padding-left: 20px;
    height: 50px;
    color: #444;
    line-height: 50px;
}
.info img{
    width: 200px;
    height: 100px;
    margin: 0 20px;
}
.tupian {
    width: 800px;
    height: 100px;

    margin: 0 auto;
    justify-content: space-between;
    padding: 20px 0;
}

</style>



